import React from 'react';

import { Link } from 'react-router-dom';

import { Figure } from 'react-bootstrap';

const MovieInfo = ({movieInfo, id}) => {
    return (
        <>
        <Link to="/">Home</Link>
        <Figure>
            <Figure.Image
                height={500}
                alt="poster"
                src="10.jpg"
            />
            <Figure.Caption>
                Nulla vitae elit libero, a pharetra augue mollis interdum.
            </Figure.Caption>
        </Figure>
        </>
    );
}

export default MovieInfo;