import React, { Component } from 'react';
import { Switch, Route } from 'react-router-dom';
import axios from 'axios';

import Home from '../pages/Home';
import MovieInfo from '../pages/MovieInfo';
import PageNotFound from '../pages/PageNotFound';

import { URL, API_KEY } from '../config/config';

import { Jumbotron, Container } from 'react-bootstrap';


class App extends Component {
	constructor(props){
		super(props);

		this.state = {
			inputValue: '',
			releaseDate: '',
			movieId: null,
			searchResponse: [],

		}

		this.handleChangeInput = this.handleChangeInput.bind(this);
		this.handleChangeSelect = this.handleChangeSelect.bind(this);
		this.handleTableClick = this.handleTableClick.bind(this);
		this.handleCloseModal = this.handleCloseModal.bind(this);		
	}



	handleChangeInput(e){
		this.setState({
			inputValue: e.target.value
		}, this.handleSearch);
	}

	handleChangeSelect(e){
		this.setState({
			releaseDate: e.target.value
		}, this.handleSearch);		
	}

	handleTableClick(e){
		this.setState({
			movieId: e.target.parentNode.id,
			showModal: true
		});
	}

	handleCloseModal(){
		this.setState({
			showModal: false
		});
	}

	handleSearch(){
		const { inputValue, releaseDate } = this.state;

		if(!inputValue){
			return null
		}

		const queryString = `${URL}/search/movie?api_key=${API_KEY}&query=${inputValue}` 
			+(releaseDate ? `&primary_release_year=${releaseDate}` : '');

		axios.get(queryString).then(response => {
			this.setState({
				searchResponse: response.data.results
			});
		});		
	}

	render(){
		const { searchResponse, inputValue, movieId } = this.state;
		const { form, table, movieInfo } = this.props;
		const showTable = (inputValue && searchResponse.length > 0) ? true : false;

		return (
			<Jumbotron fluid>
		      <Container>
					<Switch>
						<Route exact path ="/" render={() => 
							<Home 
								form={form}
								onChangeInput={this.handleChangeInput} 
								onChangeSelect={this.handleChangeSelect}
								inputValue={inputValue}
								table={table}
								response={searchResponse} 
								onTableClick={this.handleTableClick}
								showTable={showTable}
								/>
							} 
						/>
						<Route exact path ="/movieinfo" render={() => 
							<MovieInfo
								movieInfo={movieInfo}
								id={movieId}
							/>
							} 
						/>						
						<Route component={PageNotFound} />
					</Switch>	 
		      </Container>
		    </Jumbotron>		
		);		
	}
}

export default App;